.section__title {
  margin-bottom: 10px;
  font-family: var(--decorative-font);
  font-weight: normal;
  font-size: 24px;
  line-height: 1.25;
  text-align: center;

  @media screen and (min-width: 1280px) {
    margin-bottom: 20px;
    font-size: 36px;
  }
}

.section__subtitle {
  margin-bottom: 20px;
  line-height: 1.25;
  text-align: center;

  @media screen and (min-width: 1280px) {
    font-size: 18px;
    line-height: 1.22;
  }
}
